.sections {
    flex-direction: column;
}

.sections > div {
    height: 100vh;
    width: 100vw;
    opacity: 0;
    transform: translateY(-2vh);

    animation: showSections 1.2s ease 0.4s 1 forwards;
}

.Leaves img {
    position: absolute;
    height: 4vh;

    opacity: 0.15;
}

@keyframes showSections {
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}